import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import eMATPrivacyPolicy from '../views/eMATPrivacyPolicy.vue'
import eMATDeleteAccount from '../views/eMATDeleteAccount.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/e-mat/privacy-policy',
    name: 'eMATPrivacyPolicy',
    component: eMATPrivacyPolicy
  },
  {
    path: '/e-mat/delete-account',
    name: 'eMATDeleteAccount',
    component: eMATDeleteAccount
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router